import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { TreeModule, TreeNode, Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { PerformanceBean } from '../bean/performance.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { PerformanceSetDeleteBean } from './bean/delete.bean';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Router } from '@angular/router';
@Component({
    selector: 'app-performance-set',
    templateUrl: './performance-set.component.html',
    styleUrls: ['./performance-set.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class PerformanceSetComponent implements OnInit {
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    performanceBean: PerformanceBean = new PerformanceBean();
    msgs: Message[] = [];
    files: TreeNode[];
    zhibiaoName: any;
    zhibiaoID: any;
    cols: any[];
    cars: any[];
    array: any[] = [];//指标维度
    tempArr = [];
    codename: any;
    arrayTemp: any = [];//已选择的指标
    param: any = {};
    temp = []
    treeDate: TreeNode[] = [];
    performanceList: any = []
    dataTable: any = [];
    currentList: any = [];
    currentRank: string;
    selectedFiles;
    performanceSetDeleteBean: PerformanceSetDeleteBean = new PerformanceSetDeleteBean();
    userId: string;
    constructor(
        public changeDetectorRef: ChangeDetectorRef,
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public router: Router
    ) {
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.httpService.queryIndicatorTree({ inputParams: { sceneType: '001', indicatorKindId: 'PVL' } }).subscribe(data => {//指标树查询
            if (data.returnCode.code == 'success') {
                if (data.outputParams.indicatorMap && data.outputParams.indicatorMap.subIndicatorList) {
                    this.treeDate = this.toChangeTree(data.outputParams.indicatorMap.subIndicatorList);
                    console.log(this.treeDate)
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })

        this.querySelfDefineIndicator();
        // this.performanceBean.rank1 = '2';
    }

    //初始化
    ngOnInit() {
        this.cols = [
            { field: 'title', header: '指标排序维度' },
            { field: 'up', header: '上升' },
            { field: 'down', header: '下降' },
            { field: 'delete', header: '删除' }
        ]
        // this.array = []
        this.cars = [
            {
                title: '存款FTP折前',
                up: '1',
                down: '2',
                delete: '3'
            },
            {
                title: '存款FTP折前2223',
                up: '3',
                down: '3',
                delete: '4'
            },
            {
                title: '存款FTP折前22234',
                up: '3',
                down: '3',
                delete: '4'
            },
            {
                title: '存款FTP折前222345',
                up: '3',
                down: '3',
                delete: '4'
            }
        ]
    }

    //
    querySelfDefineIndicator() {
        this.httpService.querySelfDefineIndicator({ inputParams: { sceneType: '001', userId: this.userId } }).subscribe(data => {//自定义指标查询
            if (data.returnCode.code == 'success') {
                this.arrayTemp = _.clone(data.outputParams.indicatorList);
                this.array = _.clone(data.outputParams.indicatorList);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }

    //上
    up(car) {
        let carList = JSON.parse(JSON.stringify(this.arrayTemp));
        this.cars = [];
        let i = this.getIndex(carList, car);
        if (i != -1 && i != 0) {
            let a = carList[i - 1];
            carList[i] = a;
            carList[i - 1] = car;
        }
        this.arrayTemp = carList;
        if (carList && carList.length != 0) {
            let lenth = this.array.length;
            this.array = _.clone(this.arrayTemp.concat(this.array.slice(carList.length, lenth)));
        }
    }

    //下
    down(car) {
        let carList = JSON.parse(JSON.stringify(this.arrayTemp));
        this.cars = [];
        let i = this.getIndex(carList, car);
        if (i < carList.length - 1) {
            let a = carList[i + 1];
            carList[i] = a;
            carList[i + 1] = car;
        }
        this.arrayTemp = carList;
        if (carList && carList.length != 0) {
            let lenth = this.array.length;
            this.array = _.clone(this.arrayTemp.concat(this.array.slice(carList.length, lenth)));
        }
    }

    //删除
    delete(car) {
        this.currentList = [];
        this.currentList.push(car);
        this.performanceSetDeleteBean.infoList = this.currentList;
        let k = this.getIndex(this.array, car);
        if (k != -1) {
            this.array.splice(k, 1);
        }
        let carList = JSON.parse(JSON.stringify(this.arrayTemp));
        let i = this.getIndex(carList, car);
        this.cars = [];
        if (i != -1) {
            carList.splice(i, 1);
        }
        this.arrayTemp = carList;
        this.save();
    }

    //移除自定义
    deleteClick(item, i) {
        this.currentList = [];
        this.array.splice(i, 1);
    }

    getIndex(list, item) {
        let index = -1;
        for (let i = 0; i < list.length; i++) {
            if (list[i].indicatorId == item.indicatorId) {
                index = i;
                break;
            }
        }
        return index
    }

    //树选择
    nodeCheck(event) {
        console.log(event);
        if (event.node.id == '1') {
            if (event.node.value && event.node.value != '' && event.node.label && event.node.label != '') {
                let flag = false;
                this.codename = {
                    indicatorId: event.node.value,
                    indicatorAlias: event.node.label
                }
                this.array.forEach(element => {
                    if (event.node.value == element.indicatorId) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: '已选中指标不可再选' });
                        return flag = true;
                    }
                });
                if (!flag) {
                    this.array.push(this.codename);
                }
            }
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: '此指标不可选' });
            return;
        }
    }

    //树展开
    nodeCheck1(event) {
        console.log(event);
        let params = {
            inputParams: { sceneType: '001', indicatorKindId: event.node.value }
        }
        this.httpService.queryIndicatorTree(params).subscribe(data => {
            let temp = data.outputParams.indicatorMap.subIndicatorList;
            if (temp && temp.length != 0) {
                event.node.children = this.toChangeTree(temp);
            } else {
                event.node.children = [];
            }
        })
    }

    //确定
    doThis() {
        this.arrayTemp = _.clone(this.array);
    }

    //重置
    reset() {
        let bean = {
            inputParams: {}
        };
        bean['inputParams']['sceneType'] = '001';
        bean['inputParams']['operType'] = '3';
        bean['inputParams']['userId'] = this.userId;
        this.httpService.modifySelfDefineIndicator(bean).subscribe(data => {
            this.querySelfDefineIndicator();
        })
    }

    //保存
    save() {
        let bean = {
            inputParams: {}
        };
        let arrTemp = [], objTemp = {};
        if (this.arrayTemp && this.arrayTemp.lenth != 0) {
            this.arrayTemp.forEach(element => {
                arrTemp.push({ indicatorId: element['indicatorId'] })
            });
        }
        bean['inputParams']['indicatorIdGroup'] = JSON.stringify(arrTemp);
        bean['inputParams']['sceneType'] = '001';
        bean['inputParams']['operType'] = '1';
        bean['inputParams']['userId'] = this.userId;
        this.httpService.modifySelfDefineIndicator(bean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: 'Success Message', detail: '保存成功' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }
    //返回工作台
    comeback() {
        this.router.navigate(["pages/tzb/uisftech-view/workdesk"])
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

    //
    toChangeTree(param: Array<any>) {
        let tempArr = [];
        param.forEach(element1 => {
            let tempObj = {};
            if (element1.isAtomIndicator == '1') {//原子指标
                tempObj['id'] = 1;
                tempObj['value'] = element1.indicatorId;
                tempObj['label'] = element1.indicatorAlias;
                tempObj['label1'] = element1.indicatorName;
                tempObj['expanded'] = true;
                if (this.array && this.array.length != 0) {
                    this.array.forEach(element2 => {
                        if (element1.indicatorId == element2.indicatorId) {
                            tempObj['selectable'] = false;
                        }
                    });
                }
            } else {
                tempObj['value'] = element1.indicatorKindId;
                tempObj['label'] = element1.indicatorKindAlias;
                tempObj['label1'] = element1.indicatorKindName;
            }
            tempArr.push(tempObj);
        });
        return tempArr;
    }
}

