import { Component, OnInit, ViewChild, OnChanges } from '@angular/core';
import { Router } from '@angular/router';
import { TreeNode } from 'primeng/primeng';
import { ConfirmDialogModule, ConfirmationService, Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { AchievementIndcatorQryEmpHistoryBean } from './bean/achievementIndcatorQryEmpHistory.bean';//个人历史
import { AchievementIndcatorQryOrgHistoryBean } from './bean/achievementIndcatorQryOrgHistory.bean';//机构历史
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD, TZB_HTTP_CUS } from 'app/@uisftech/common/service/http.constant';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { MenubarModule, MenuItem } from 'primeng/primeng';
import { OnlyYearMonthSelectComponent } from 'app/@uisftech/common/directive/monthly-calendar/monthly-calendar/monthly-calendar.component';
import * as _ from 'lodash';
import * as moment from 'moment';
@Component({
    selector: 'app-performance-history',
    templateUrl: './performance-history.component.html',
    styleUrls: ['./performance-history.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class PerformanceHistoryComponent implements OnInit, OnChanges {
    chooseNum: number;
    items: MenuItem[];
    first: number = 0;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    treePerName: any;
    treePerId: any;
    treeOrgName: any;
    treeOrgID: any;
    orgTreePage: boolean = false;
    dateArr: any;
    msgs: Message[] = [];
    //总页数
    personTotal: string;
    adminTotal: string;
    //每页多少条
    pageSize: string;
    startDt: any;  //日期起
    endDt: any; //日期止
    startDtPer: any;
    endDtPer: any;
    ch: any = CALENDAR_CN;
    temData: Date = new Date();
    userform: FormGroup;
    scopeform: FormGroup;
    adminAddPage: boolean = false;
    personAddPage: boolean = false;
    personAddHeader = '选择字段';
    isAll: boolean = false;
    isAll2: boolean = false;
    dateOption: any = [];//日期
    ////////////////////
    showthis: boolean = true;
    //默认个人历史表头
    perTitle: any = [];
    //选择字段个人
    arrListPerson: any = [];
    // adminHistory: any[];//管理角色历史业绩
    //管理历史表头
    adminTtitle: any = [];
    //选择字段机构
    arrListAdmin: any = [];
    userDisplay: boolean = false;
    flag: boolean = false;
    personData = [];
    adminData = [];
    adminPagebtn: boolean = false;
    adminPage: boolean = true;
    personPage: boolean = false;
    achievementIndcatorQryOrgHistoryBean: AchievementIndcatorQryOrgHistoryBean = new AchievementIndcatorQryOrgHistoryBean();
    achievementIndcatorQryEmpHistoryBean: AchievementIndcatorQryEmpHistoryBean = new AchievementIndcatorQryEmpHistoryBean();
    fileName: any;//下载文件名称
    filePath: any;//下载文件路径
    DOWNLOAD: string = TZB_HTTP_CUS;
    hrefname: any;
    item: number = 1;
    chooseMonth;
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    extParam: any;

    nameShow: boolean = false;//校验姓名

    giveRet;
    showMonth: boolean = true;
    showmonthAdmin: boolean = true;
    blockDisplay: any = {
        flag: false
    }
    @ViewChild(OnlyYearMonthSelectComponent) child: OnlyYearMonthSelectComponent

    constructor(
        public router: Router,
        public httpService: CusOperationHttpService,
        public fb: FormBuilder,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        if (this.user.extParam) {
            this.extParam = this.user.extParam;
        }
        if (this.extParam.managePermission == 'N' || !this.permissionCheck('SID00007_P002_P003_P001')) {
            this.adminPage = false;
            // this.adminPagebtn = true;
            this.personPage = true;
            this.item = 2;
        }
        // if (this.permissionCheck('SID00007_P002_P003_P001')) {
        //     this.adminPage = true;
        //     this.adminPagebtn = true;
        //     this.item = 1;
        // } else {
        //     this.adminPage = false;
        //     this.adminPagebtn = false;
        //     this.personPage = true;
        //     this.item = 2;
        // }

        let data = new Date();
        this.endDt = moment(data).format('YYYY-MM');//日期止
        this.startDt = moment(data).add(-1, 'year').format('YYYY-MM');//日期起
        this.endDtPer = moment(data).format('YYYY-MM');
        this.startDtPer = moment(data).add(-1, 'year').format('YYYY-MM');
    }
    ngOnInit() {
        this.httpService.queryIndicatorInfoBySceneType({ inputParams: { sceneType: '001' } }).subscribe(data => {//查询所有字段
            if (data.returnCode.code == 'success') {
                let resultList1 = [];
                let resultList2 = [];
                let arrTemp1 = [];
                let arrTemp2 = [];
                let isCheck;
                if (data.outputParams.resultList && data.outputParams.resultList.length != 0) {
                    data.outputParams.resultList.forEach(element => {
                        if (element.isShow == '1') {
                            isCheck = true;
                            arrTemp1.push({ indicatorId: element.indicatorId, indicatorAlias: element.indicatorAlias });
                            arrTemp2.push({ indicatorId: element.indicatorId, indicatorAlias: element.indicatorAlias });
                        } else {
                            isCheck = false;
                        }
                        resultList1.push({ indicatorId: element.indicatorId, indicatorAlias: element.indicatorAlias, isCheck: isCheck });
                        resultList2.push({ indicatorId: element.indicatorId, indicatorAlias: element.indicatorAlias, isCheck: isCheck });
                    });
                }
                this.adminTtitle = _.clone(arrTemp1);//机构表头
                this.perTitle = _.clone(arrTemp2);//个人表头
                this.arrListAdmin = _.clone(resultList1);//机构自定义字段
                this.arrListPerson = _.clone(resultList2);//个人自定义字段
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });

        if (this.adminPage) {
            this.queryAdmin(1);//默认查询机构
        } else {
            this.queryPerson(1);
        }
    }
    ngOnChanges() {

    }
    //翻页
    paginate(event, item) {
        if (item == 'person') {
            //每页显示的条数
            this.achievementIndcatorQryEmpHistoryBean.pageSize = event.rows * 1;
            //当前页
            this.achievementIndcatorQryEmpHistoryBean.pageNum = event.page + 1
            this.first = event.page * this.achievementIndcatorQryEmpHistoryBean.pageSize;
            this.queryPerson(2);
        } else if (item == 'admin') {
            //每页显示的条数
            this.achievementIndcatorQryOrgHistoryBean.pageSize = event.rows * 1;
            //当前页
            this.achievementIndcatorQryOrgHistoryBean.pageNum = event.page + 1;
            this.first = event.page * this.achievementIndcatorQryOrgHistoryBean.pageSize;
            this.queryAdmin(2);
        }
    }
    //日期
    showData() {
        if (this.commfunc.transDateN(this.startDtPer) > this.commfunc.transDateN(this.endDtPer)) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '到期日期必须大于开始日期!' }];
            return;
        } else if (this.commfunc.transDateN(this.startDt) > this.commfunc.transDateN(this.endDt)) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '到期日期必须大于开始日期!' }];
            return;
        }
    }
    //机构业绩查询
    queryFirst() {
        this.achievementIndcatorQryEmpHistoryBean.pageSize = 10;
        this.achievementIndcatorQryEmpHistoryBean.pageNum = 1;
        this.first = 0;
        this.queryPerson(2);
    }
    //历史业绩查询(个人角色)
    queryPerson(i) {
        if (!this.nameShow) {
            if (this.startDtPer > this.endDtPer) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '到期日期必须大于开始日期!' }];
                return;
            } else {
                let params = {
                    inputParams: {
                        userName: this.achievementIndcatorQryEmpHistoryBean.custName,
                        userId: this.achievementIndcatorQryEmpHistoryBean.empName,
                        orgId: this.treePerId,
                        startDataDt: this.startDtPer ? moment(this.startDtPer).format('YYYY-MM') : this.startDtPer,
                        endDataDt: this.endDtPer ? moment(this.endDtPer).format('YYYY-MM') : this.endDtPer,
                        objType: '1',
                        pageNum: this.achievementIndcatorQryEmpHistoryBean.pageNum,
                        pageSize: this.achievementIndcatorQryEmpHistoryBean.pageSize
                    }
                }
                this.httpService.queryHistoryPerformance(params).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.personData = data.outputParams.currDataList;
                        this.personTotal = data.outputParams.totalNum;
                    } else {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                });
            }
        }
    }
    //个人姓名校验-汉字
    cusName() {
        if (this.achievementIndcatorQryEmpHistoryBean.custName) {
            let reg = /^[\u4E00-\u9FA5]{1,6}$/;
            let checkName = reg.test(this.achievementIndcatorQryEmpHistoryBean.custName);
            if (!checkName) {
                this.nameShow = true;
                return;
            } else {
                this.nameShow = false;
                return;
            }
        }
    }
    //个人业绩查询
    querySecond() {
        this.achievementIndcatorQryOrgHistoryBean.pageSize = 10;
        this.achievementIndcatorQryOrgHistoryBean.pageNum = 1;
        this.first = 0;
        this.queryAdmin(2);
    }
    //历史业绩查询(机构角色)
    queryAdmin(i) {
        if (this.startDt > this.endDt) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '到期日期必须大于开始日期!' }];
            return;
        } else {
            let params = {
                inputParams: {
                    orgId: this.treeOrgID,
                    startDataDt: this.startDt ? moment(this.startDt).format('YYYY-MM') : this.startDt,
                    endDataDt: this.endDt ? moment(this.endDt).format('YYYY-MM') : this.endDt,
                    objType: '2',
                    pageNum: this.achievementIndcatorQryOrgHistoryBean.pageNum,
                    pageSize: this.achievementIndcatorQryOrgHistoryBean.pageSize
                }
            }
            this.httpService.queryHistoryPerformance(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.adminData = data.outputParams.currDataList;
                    this.adminTotal = data.outputParams.totalNum;
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            });
        }
    }
    //个人历史业绩数据导出下载
    outputPerson(i) {
        if (Number(this.personTotal) > 50000) {
            this.msgs = [];
            this.msgs = [{ severity: 'info', summary: '提示', detail: '最多支持导出5万条数据' }];
        }
        this.blockDisplay.flag = true;
        let indicatorIdList = ['dataDt', 'userName'];
        if (this.perTitle && this.perTitle.length != 0) {
            this.perTitle.forEach(element => {
                indicatorIdList.push(element.indicatorId);
            });
        }
        if (!this.nameShow) {
            let params = {
                inputParams: {
                    exportType: i,
                    userName: this.achievementIndcatorQryEmpHistoryBean.custName,
                    userId: this.achievementIndcatorQryEmpHistoryBean.empNo,
                    orgId: this.treePerId,
                    startDataDt: this.startDtPer ? moment(this.startDtPer).format('YYYY-MM') : this.startDtPer,
                    endDataDt: this.endDtPer ? moment(this.endDtPer).format('YYYY-MM') : this.endDtPer,
                    objType: '1',
                    pageNum: this.achievementIndcatorQryOrgHistoryBean.pageNum,
                    pageSize: this.achievementIndcatorQryOrgHistoryBean.pageSize,
                    indicatorIdList
                }
            }
            this.httpService.exportPerformance(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.fileName = data.outputParams.fileName;
                    this.filePath = data.outputParams.fileUrl;
                    this.blockDisplay.flag = false;
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '提示', detail: '下载成功' }];
                    open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(this.fileName) + "&fileUrl=" + this.filePath)
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            });
        }
    }
    //机构导出
    outputAdmin(i) {
        if (Number(this.adminTotal) > 50000) {
            this.msgs = [];
            this.msgs = [{ severity: 'info', summary: '提示', detail: '最多支持导出5万条数据' }];
        }

        this.blockDisplay.flag = true;
        let indicatorIdList = ['dataDt', 'orgId', 'orgName'];
        if (this.adminTtitle && this.adminTtitle.length != 0) {
            this.adminTtitle.forEach(element => {
                indicatorIdList.push(element.indicatorId);
            });
        }
        let params = {
            inputParams: {
                exportType: i,
                orgId: this.treeOrgID,
                startDataDt: this.startDt ? moment(this.startDt).format('YYYY-MM') : this.startDt,
                endDataDt: this.endDt ? moment(this.endDt).format('YYYY-MM') : this.endDt,
                objType: '2',
                indicatorIdList
            }
        }
        this.httpService.exportPerformance(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.fileName = data.outputParams.fileName;
                this.filePath = data.outputParams.fileUrl;
                this.blockDisplay.flag = false;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '下载成功' }];
                open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(this.fileName) + "&fileUrl=" + this.filePath + "")
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    //自定义设置
    toSet() {
        this.router.navigate(["pages/tzb/custom/performance/performance-set"])
    }
    adminChoose(i) {
        this.adminPage = true;
        this.personPage = false;
        this.item = i;
        this.achievementIndcatorQryOrgHistoryBean = new AchievementIndcatorQryOrgHistoryBean();
        this.treeOrgName = '';
        this.treeOrgID = '';
        this.first = 0;
        this.queryAdmin(1);
    }
    personChoose(i) {
        if (this.flag) {
            return
        }
        this.personPage = true;
        this.adminPage = false;
        this.item = i;
        this.achievementIndcatorQryEmpHistoryBean = new AchievementIndcatorQryEmpHistoryBean();
        this.treePerName = '';
        this.first = 0;
        this.queryPerson(1);
    }
    chooseBtn() {
        this.personAddPage = true;
    }
    save() {
        this.personAddPage = false;
    }
    //个人单选
    doSelect(data) {
        let all = true;
        if (data.isCheck) {
            this.perTitle.push(data);
            this.arrListPerson.forEach(item => {
                all = item.isCheck && all
            });
        } else {
            try {
                this.perTitle.forEach((item, i) => {
                    if (item.indicatorId == data.indicatorId) {
                        this.perTitle.splice(i, 1);
                        throw new Error();
                    }
                })
            } catch (error) {
                all = false;
            }
        }
        // all = false;
        this.perTitle.sort((a, b) => {
            return a.orderCode > b.orderCode
        })
        this.isAll = all;
        console.log(this.perTitle);
    }
    //个人全选
    checkAll() {
        debugger
        let isTrue = this.isAll;
        this.perTitle = [];
        this.arrListPerson.forEach(item => {
            item['isCheck'] = isTrue;
        });
        if (this.isAll) {
            this.perTitle = _.clone(this.arrListPerson);
        }
    }
    //机构字段
    chooseBtnAdmin() {
        this.adminAddPage = true;
    }
    saveAdmin() {
        this.adminAddPage = false;
    }
    //机构全选
    checkAllAdmin() {
        let isTrue = this.isAll2;
        this.adminTtitle = [];
        this.arrListAdmin.forEach(item => {
            item['isCheck'] = isTrue;
        });
        if (this.isAll2) {
            this.adminTtitle = _.clone(this.arrListAdmin);
        }
    }
    //机构单选
    doSelectAdmin(param) {
        let all = true;
        if (param.isCheck) {
            this.adminTtitle.push(param);
            this.arrListAdmin.forEach(item => {
                all = item.isCheck && all
            });
        } else {
            try {
                this.adminTtitle.forEach((item, i) => {
                    if (item.indicatorId == param.indicatorId) {
                        this.adminTtitle.splice(i, 1);
                        throw new Error();
                    }
                })
            } catch (error) {
                all = false;
            }
        }
        this.isAll2 = all;
        console.log(this.adminTtitle);
    }
    orgTreeShow() {
        this.orgTreePage = true;
        this.chooseNum = 2;
    }
    treeCallPer(param) {
        this.orgTreePage = false;
        this.treePerName = param.orgName;
        this.treePerId = param.orgId;
    }
    treeCallAdmin(param) {
        this.orgTreePage = false;
        this.treeOrgName = param.orgName;
        this.treeOrgID = param.orgId;
    }
    perTreeShow() {
        this.orgTreePage = true;
        this.chooseNum = 1;
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    comeback() {
        this.router.navigate(["pages/tzb/uisftech-view/workdesk"]);
    }
    //部门重置
    resetAdmin() {
        this.treeOrgName = '';
        this.treeOrgID = '';
        this.startDt = '';
        this.endDt = '';
        this.showmonthAdmin = !this.showmonthAdmin;
    }
    //个人重置
    resetPer() {
        this.achievementIndcatorQryEmpHistoryBean.custName = '';
        this.achievementIndcatorQryEmpHistoryBean.empNo = '';
        this.achievementIndcatorQryEmpHistoryBean.empName = '';
        this.treePerName = '';
        this.treePerId = '';
        this.startDtPer = '';
        this.endDtPer = '';
        // this.showMonth = false;
        this.showMonth = !this.showMonth;
    }
    //////////////////////
    tabChange(event) {
        let gundam_class: any = document.getElementsByClassName('gundam');
        gundam_class[event.index].children[0].style.width = '100%';
        this.showthis = false;
        this.showthis = true;
    }

    userChoose() {
        this.userDisplay = true;
    }

    returnOwnerInfo(event) {
        console.log(event);
        this.userDisplay = false;
        this.achievementIndcatorQryEmpHistoryBean.empName = event[0].tellerName;
        this.achievementIndcatorQryEmpHistoryBean.empNo = event[0].tellerId;
    }

    tellerCalll() {
        let params = {
            tellerId: this.achievementIndcatorQryEmpHistoryBean.empName
        }
        this.httpService.queryTellerById(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.achievementIndcatorQryEmpHistoryBean.custName = data.tellerName;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
}