import { Component, OnInit } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';

@Component({
    selector: 'institutions-mapping',
    templateUrl: './institutions-mapping.component.html',
    styleUrls: ['./institutions-mapping.component.scss'],
    providers: [Commfunc, ConfirmationService]
})

export class InstitutionsMappingComponent implements OnInit {

    tableMesg: any = tableMessage;//表格中无数据时显示的内容
    msgs: Message[] = [];
    mapList: any[];
    first: number = 1;
    pageNum: number = 1;
    pageSize: number = 10;
    total: number;
    display: boolean = false;

    coreOrgId: string;
    coreOrgName: string;
    hrOrgName: string;
    hrOrgId: string;
    bankCode: string;

    inValue: any;

    constructor(
        public httpService: CustomeHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
    ) {

    }

    ngOnInit() {
        this.query();//人力与核心机构映射信息查询
    }

    query() {
        let params = {
            inputParams: {
                coreOrgId: this.coreOrgId,
                coreOrgName: this.coreOrgName,
                hrOrgId: this.hrOrgId,
                hrOrgName: this.hrOrgName,
                bankCode: this.bankCode,
                pageNum: this.pageNum,
                pageSize: this.pageSize
            }
        }
        this.httpService.queryCoreandhrOrgMapping(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.mapList = data.outputParams.resultList;
                this.total = data.outputParams.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    reset() {
        this.coreOrgId = '';
        this.coreOrgName = '';
        this.hrOrgId = '';
        this.hrOrgName = '';
        this.query();
    }

    add() {
        this.display = true;
        this.inValue = '';
    }

    delete(col) {
        this.confirmationService.confirm({
            message: '确认删除?',
            accept: () => {
                let params = {
                    inputParams: {
                        coreOrgId: col.coreOrgId,
                        hrOrgId: col.hrOrgId,
                        seqId: col.seqId
                    }
                }
                this.httpService.deleteCoreandhrOrgMapping(params).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.query()
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                });
            }
        });
    }

    outValue(event){
        this.display = event;
        this.query();
    }

    update(col) {
        console.log(col);
        this.display = true;
        this.inValue = col;
    }

    //分页
    paginate(event) {
        let rows = event.rows * 1;//每页显示的条数
        this.pageSize = rows;//当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = event.page * this.pageSize;
        this.query();//调用查询的方法
    }
}