import { Component, AfterViewInit, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TreeNode, Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { PerformanceBean } from '../bean/performance.bean';
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { DecimalPipe } from '@angular/common';
@Component({
    selector: 'performance-details-org',
    templateUrl: './performance-details-org.component.html',
    styleUrls: ['./performance-details-org.component.scss'],
    providers: [CustomeHttpService, CusOperationHttpService, Commfunc, DecimalPipe]
})
export class PerformanceDetailsOrgComponent implements OnInit {
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    date: Date = new Date();
    msgs: Message[] = [];
    userform: FormGroup;
    scopeform: FormGroup;
    perDetail: any;//个人指标详情    
    adminDetail: any;//管理指标详情
    titList: any = [];
    dataList: any = [];
    LastMonthList: any = [];
    LastYearList: any = [];
    LastPeriodList: any = [];
    rank: any = [];

    titAdminList: any;
    AdminDataList: any;
    AdminDataOrgList: any;
    temp: any;

    admin: boolean = false;
    person: boolean = false;
    performanceBean: PerformanceBean = new PerformanceBean();
    outName: any;//下载文件名称
    outPath: any;//下载文件路径
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userIdT: string = '';
    userName: string = '';
    orgName: string = '';
    extParam: any;
    newdate: any;
    operatingLevel: string;
    userDisplay: boolean = false;
    orgDisplay: boolean = false;
    display: boolean = false;
    FTPOption: any;
    lineTU: any;
    indicatorsType: any[] = [
        { value: '01', label: '元' },
        { value: '02', label: '万元' },
        { value: '03', label: '张' },
        { value: '04', label: '个' },
        { value: '05', label: '位' },
    ];//指标类型
    constructor(
        public router: Router,
        public httpService: CustomeHttpService,
        public fb: FormBuilder,
        public httpServiceCus: CusOperationHttpService,
        public commfunc: Commfunc,
        public decimalPipe: DecimalPipe

    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.extParam) {
            this.extParam = this.user.extParam;
        }
        this.userIdT = this.user.userId;
    }
    ngOnInit() {
        if (this.user.extParam.managePermission == 'Y') {
            this.query('init');
        }
        // this.newdate = this.transDate(this.date);
    }
    ngAfterViewInit() {
        let gundam_details = document.getElementById('gundam-details').getElementsByTagName('th');
        gundam_details[0].style.textAlign = 'center';
    }

    userChoose() {
        this.userDisplay = true;
    }

    returnOwnerInfo(event) {
        console.log(event);
        this.userDisplay = false;
        this.userName = event[0].tellerName;
        this.userId = event[0].tellerId;
    }

    orgChoose() {
        this.orgDisplay = true;
    }

    returnOrg(event) {
        console.log(event);
        this.orgDisplay = false;
        this.orgName = event.orgName;
        this.orgId = event.orgId;
    }

    reset() {
        this.userName = '';
        this.userId = '';
        this.orgName = '';
        this.orgId = '';
        this.query();
    }

    query(data1?) {
        let params
        if (data1 && data1 == 'init') {
            params = {
                inputParams: {
                    drillDownType: '1'
                }
            }
        } else {
            params = {
                inputParams: {
                    drillDownType: '2',
                    userId: this.userId,
                    orgId: this.orgId,
                }
            }
        }
        this.httpServiceCus.queryMorePerformance(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.newdate = data.outputParams.dataDt;
                this.titAdminList = data.outputParams.titleList;
                let arrTemp1 = [];
                if (data1 && data1 == 'init') {
                    arrTemp1.push(data.outputParams.parentDataMap);
                    this.AdminDataList = this.toChangeTree(arrTemp1);
                    this.AdminDataList[0]['children'] = this.toChangeTree(data.outputParams.currDataList);
                    this.AdminDataList[0]['expanded'] = true;
                } else {
                    if (data.outputParams.parentDataMap && JSON.stringify(data.outputParams.parentDataMap) != '{}') {
                        arrTemp1.push(data.outputParams.parentDataMap);
                        this.AdminDataList = this.toChangeTree(arrTemp1);
                    } else if (data.outputParams.currDataList && data.outputParams.currDataList.length != 0) {
                        this.AdminDataList = this.toChangeTree(data.outputParams.currDataList);
                    } else {
                        this.AdminDataList = [];
                        if (this.orgId) {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: '无法搜索到机构业绩数据' });
                        } else if (this.userId) {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: '无法搜索到员工业绩数据' });
                        }
                    }
                }
                this.adminDetail = this.AdminDataList;
                console.log(this.adminDetail);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    //日期格式转换处理
    transDate(value) {
        let time = new Date(value);
        let year = value.getFullYear();
        let month = value.getMonth() + 1 + '';
        let data = value.getDate() + '';
        if (month.length < 2) {
            month = '0' + month;
        }
        if (data.length < 2) {
            data = "0" + data
        }
        let newtime = year + "-" + month + "-" + data;
        return newtime;
    }
    //
    toChangeTree(data: any[]) {
        let tempArr = [];
        if (data) {
            data.forEach(item => {
                let tempObj = {};
                tempObj['data'] = item;
                tempObj['children'] = [{}];
                if (item.objType == 1) {
                    tempObj['expanded'] = true;
                    tempObj['children'] = [];
                }
                tempArr.push(tempObj);
            });
        }
        return tempArr;
    }
    //获取日期组件的数据
    showdata(event) {
        this.performanceBean.startDt = this.transDate(event[0]);//开始日期
        this.performanceBean.endDt = this.transDate(event[1]);//结束日期
    }

    //自定义设置
    toSet() {
        this.router.navigate(["pages/tzb/custom/performance/performance-set"])
    }
    //历史业绩
    toHistory() {
        this.router.navigate(["pages/tzb/custom/performance/performance-history"])
    }
    comeback() {
        this.router.navigate(["pages/tzb/uisftech-view/workdesk"])
    }
    nodeExpand(event) {
        console.log(event)
        if (!event.node.data.obj) {
            event.node.children = [];
            return;
        }
        if (event.node['expanded']) {
            event.node['expanded'] = false;
            return
        }
        let params = {
            inputParams: {
                orgId: event.node.data.obj,
                drillDownType: '3',
            }
        }
        this.httpServiceCus.queryMorePerformance(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.outputParams.currDataList.length == 0) {
                    event.node.children = [];
                } else {
                    event.node.children = this.toChangeTree(data.outputParams.currDataList);
                    event.node['expanded'] = true;
                }
            }
            else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    nodeSelect(event) {
        console.log(event);
        console.log(event.originalEvent.srcElement)
        let source = event.originalEvent.srcElement;
        let p = source.parentNode;
        let g = Array.from(p.parentNode.children);
        g = g.filter(x => x['localName'] === 'td');
        let index = g.indexOf(p);
        if (index != 0) {
            this.showLine(this.titAdminList[index - 1], event.node.data);
        } else {
            this.nodeExpand(event);
        }
    }
    // 展示曲线图
    showLine(item, data) {
        console.log(item);
        let thiss = this;
        let title = '';
        let key = '';
        let xData = [];
        let vData = [];
        let unit = '';
        let decimalPlaces = '';
        let inputParams
        if (!item) {
            return
        }
        if (data.objType && data.obj) {
            inputParams = {
                indicatorIdList: [item.indicatorId],
                obj: data.obj,
                objType: data.objType
            }
        }
        let param = {
            inputParams: inputParams
        }
        this.httpService.queryLatelyPerformance(param).subscribe(data => {
            this.lineTU = data.outputParams.currDataList;
            if (this.lineTU && this.lineTU.length != 0) {
                unit = data.outputParams.titleList[0].unit;
                decimalPlaces = data.outputParams.titleList[0].decimalPlaces;
                title = item.indicatorAlias;
                key = item.indicatorId;
                this.lineTU.forEach((element, i) => {
                    xData.push(element.dataDt);
                    vData.push(element[key]);
                });
                this.indicatorsType.forEach(element2 => {
                    if (unit == element2.value) {
                        unit = element2.label;
                    }
                });
            }
            console.log(vData);
            let sortList = vData.map(Number).sort((x, y) => {
                return x - y;
            });
            let min = sortList[0];
            let max = sortList[sortList.length - 1];

            //配置
            this.FTPOption = {
                title: {
                    text: title,
                    // subtext:"副标题",
                    x: "center"
                },
                tooltip: {
                    trigger: 'axis',
                },
                legend: {
                    type: 'scroll',
                    orient: "vertical",
                    right: 100,
                    data: [title]
                },
                toolbox: {
                    show: false,
                    feature: {
                        dataZoom: {
                            yAxisIndex: "none"
                        },
                        dataView: { readOnly: false },
                        magicType: { type: ["line", "bar"] },
                        restore: {},
                        saveAsImage: {}
                    }
                }, //右上角7个图标
                xAxis: {
                    name: "月份（月）",
                    type: "category",
                    boundaryGap: 'dataMin',
                    data: xData,
                    axisLabel: {
                        interval: 0,
                        rotate: 15
                    }
                },
                yAxis: {
                    name: "单位:" + unit,
                    type: "value",
                    axisLabel: {
                        formatter: "{value}"
                    }
                },
                series: [
                    {
                        name: title,
                        type: "line",
                        data: vData,
                        itemStyle: {
                            normal: {
                                lineStyle: {
                                    color: '#0000FF'
                                },
                                color: function (param) {
                                    if (param.value == max) {
                                        return '#FF0000'
                                    } else if (param.value == min) {
                                        return '#00FF00'
                                    } else {
                                        return '#0000FF'
                                    }
                                }
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                formatter: function (a) {
                                    // console.log(a);
                                    if (parseInt(decimalPlaces) == 2) {
                                        var newstr = thiss.decimalPipe.transform(parseFloat(a.value), '1.2-2');
                                        return newstr;
                                    }
                                }
                            }
                        },
                    },
                ]
            }
        })
        this.display = true;
    }
    // 隐藏折线图
    hideChart() {
        this.display = false;
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)

    }

}