import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Validators, FormBuilder, FormGroup, FormControl } from '@angular/forms';

@Component({
    selector: 'institutions-mapping-relation',
    templateUrl: './institutions-mapping-relation.component.html',
    styleUrls: ['./institutions-mapping-relation.component.scss'],
    providers: [Commfunc]
})

export class InstitutionsMappingRelationComponent implements OnInit {

    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    msgs: Message[] = [];
    tableMesg: any = tableMessage;//表格中无数据时显示的内容
    legalFlag: string;
    seqId: string;
    hrOrgId: string;
    hrOrgName: string;
    coreOrgId: string;
    displayHrOrgId: string;
    displayHrOrgName: string;
    coreOrgName: string;
    contentDisplay: string;
    displayCoreOrgId: string;
    displayCoreOrgName: string;
    orgType: string;
    superiorManageOrg: string;
    first: number = 1;
    pageNum: number = 1;
    pageSize: number = 10;
    total: number;
    tableData1: any[];
    tableData2: any[];
    display: boolean = false;
    userform: FormGroup; //校验

    constructor(
        public httpService: CustomeHttpService,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {

    }

    ngOnInit() {
        this.userform = this.fb.group({
            'hrOrgId': new FormControl('', [Validators.required, Validators.pattern(/^[0-9]*$/), Validators.maxLength(20)]),
            'hrOrgName': new FormControl('', [Validators.required]),
            'coreOrgId': new FormControl('', [Validators.required, Validators.maxLength(20)]),
            'coreOrgName': new FormControl('', [Validators.required]),
        });

        if (this.inValue) {
            this.hrOrgId = this.inValue.hrOrgId;
            this.hrOrgName = this.inValue.hrOrgName;
            this.coreOrgId = this.inValue.coreOrgId;
            this.coreOrgName = this.inValue.coreOrgName;
            this.seqId = this.inValue.seqId;
        }
    }

    choose(i) {
        this.contentDisplay = i;
        this.display = true;
        this.first = 1;
        this.pageNum = 1;
        this.pageSize = 10;
        this.total = 0;
        if (i == 1) {
            this.queryHrOrg('c');
        } else if (i == 2) {
            this.queryOrgByCombConditions('c');
        }
    }

    reset(i) {
        if (i == 1) {
            this.hrOrgId = '';
            this.displayHrOrgId = '';
            this.displayHrOrgName = '';
            this.queryHrOrg('c');
        } else if (i == 2) {
            this.displayCoreOrgId = '';
            this.displayCoreOrgName = '';
            this.orgType = '';
            this.superiorManageOrg = '';
            this.queryOrgByCombConditions('c');
        }
    }

    closeBack() {
        this.displayHrOrgId = '';
        this.displayHrOrgName = '';
        this.displayCoreOrgId = '';
        this.displayCoreOrgName = '';
        this.orgType = '';
        this.superiorManageOrg = '';
    }

    queryHrOrg(type?) {
        debugger
        let inputParams;
        if (type && type == 'c') {
            inputParams = {
                orgId: this.displayHrOrgId,
                orgName: this.displayHrOrgName,
                pageNum: this.pageNum,
                pageSize: this.pageSize,
                isLike: '1'
            }
        } else {
            if (!this.hrOrgId) {
                return
            }
            inputParams = {
                orgId: this.hrOrgId,
                isLike: '0'
            }
        }
        let params = {
            inputParams: inputParams
        }
        this.httpService.queryHrOrg(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (type == 'c') {
                    this.tableData1 = data.outputParams.resultList;
                    this.total = data.outputParams.totalNum;
                } else {
                    if (data.outputParams.resultList && data.outputParams.resultList.length != 0) {
                        if (data.outputParams.resultList.length == 1) {
                            this.hrOrgName = data.outputParams.resultList[0].orgName;
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: '条件不唯一，请通过选择' });
                        }
                    } else {
                        this.hrOrgName = '';
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    queryOrgByCombConditions(type?) {
        let params;
        if (type && type == 'c') {
            params = {
                orgId: this.displayCoreOrgId,
                orgName: this.displayCoreOrgName,
                orgType: this.orgType,
                superiorManageOrg: this.superiorManageOrg,
                flag: '0',
                startRow: this.pageNum,
                rowNum: this.pageSize
            }
        } else {
            if (!this.coreOrgId) {
                return
            }
            params = {
                orgId: this.coreOrgId,
                flag: '1',
            }
        }
        params = params
        this.httpService.queryOrgByCombConditions(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (type == 'c') {
                    this.tableData2 = data.orgInfoList;
                    this.total = data.rowCount;
                } else {
                    if (data.orgInfoList && data.orgInfoList.length != 0) {
                        if (data.orgInfoList.length == 1) {
                            this.coreOrgName = data.orgInfoList[0].orgName;
                            this.legalFlag = data.orgInfoList[0].legalFlag;
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: '条件不唯一，请通过选择' });
                        }
                    } else {
                        this.coreOrgName = '';
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    save() {
        let operType;
        if (!this.inValue) {
            operType = '1';
        } else {
            operType = '2';
        }

        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }

        let params = {
            inputParams: {
                operType: operType,
                hrOrgId: this.hrOrgId,
                coreOrgId: this.coreOrgId,
                bankCode: this.coreOrgId,
                seqId: this.seqId
            }
        }
        this.httpService.modifyCoreandhrOrgMapping(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false);
                if (!this.inValue) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功' });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功' });
                }

            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    quit() {
        this.outValue.emit(false);
    }

    onRowSelect(event, index) {
        console.log(event)
        this.display = false;
        if (index == 1) {
            this.hrOrgId = event.data.orgId;
            this.hrOrgName = event.data.orgName;
        } else {
            this.coreOrgId = event.data.orgId;
            this.coreOrgName = event.data.orgName;
            this.legalFlag = event.data.legalFlag;
        }
    }

    //分页
    paginate(event) {
        let rows = event.rows * 1;//每页显示的条数
        this.pageSize = rows;//当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = event.page * this.pageSize;
        if (this.contentDisplay == '1') {
            this.queryHrOrg('c');//调用查询的方法
        } else {
            this.queryOrgByCombConditions('c');
        }
    }
}