export class PerformanceBean{
    indcatorList:Array<indcatorList>;//业绩指标List

    //历史业绩查询(机构)
    startDt:string;//开始日期
    endDt:string;//结束日期
    qryScope:string;//查询范围
    orRelation:string;//或关系
    custName:string;//个人姓名
    empNo:string;//工号
    orgType:string;//机构类型
    orgNo:string;//机构码
    branchName:string;//分行名称
    subBranchName:string;//支行名称
    departmentName:string;//部门名称

    //业绩指标树
    indicatorCate:string;//指标大类
    indicatorSubType:string;//指标小类
    indicatorCode:string;//指标id
    //业绩指标自定义设置保存
    infoList:Array<infoList>;  
    rank1:string;//排名类别：全行或区域
    rank2:string;//排名类别：全条线或所在条线
 
    
    
}
//业绩指标List
export class indcatorList{
    indcatoraClassId:string;//指标类别ID
    indcatorClassName:string;//指标类别别称
    dimensionTypeId:string;//指标维度ID
    dimensionTypeName:string;//指标维度名称
    seqNo:number;//顺序号
  
}
//业绩指标自定义设置保存
export class infoList{
    empRole:string;
    indicatorCode:string;
    indicatorName:string;
    dimensionCode:string;
    dimensionName:string;
    seqNo:string;
}