    queryLatelyPerformance(list: any[]) {
        let arrTemp = [];
        if (list && list.length != 0) {
            list.forEach(element => {
                arrTemp.push(element.indicatorId);
            });
        }
        let params = {
            inputParams: {
                objType: 1,
                obj: this.userId,
                indicatorIdList: arrTemp
            }
        }
        this.httpServiceCus.queryLatelyPerformance(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.datenew = data.outputParams.dataDt;
                let arrTemp1 = [];
                arrTemp1.push({ dataDt: data.outputParams.objName });
                this.AdminDataList = this.toChangeTree(arrTemp1);
                this.AdminDataList[0]['children'] = this.toChangeTree(data.outputParams.currDataList);
                this.AdminDataList[0]['expanded'] = true;
                this.adminDetail = this.AdminDataList;
            }
            else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    //
    toChangeTree(data: any[]) {
        let tempArr = [];
        if (data) {
            data.forEach(item => {
                let tempObj = {};
                tempObj['data'] = item;
                tempObj['children'] = [{}];
                tempArr.push(tempObj);
            });
        }
        return tempArr;
    }
