import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { PerformanceDetailsComponent } from './performance-details/performance-details.component';//业绩详情
import { PerformanceSetComponent } from './performance-set/performance-set.component';//自定义设置
import { PerformanceHistoryComponent } from './performance-history/performance-history.component';//业绩历史
import { PerformanceDetailsOrgComponent } from './performance-details-org/performance-details-org.component';//业绩详情机构下钻
import { HistoryAdminComponent } from "app/pages/tzb/custom/performance/performance-history/history-admin/history-admin.component";
import { HistoryPersonComponent } from "app/pages/tzb/custom/performance/performance-history/history-person/history-person.component";
import { InstitutionsMappingComponent } from './institutions-mapping/institutions-mapping.component';
import { InstitutionsMappingRelationComponent } from './institutions-mapping/institutions-mapping-relation/institutions-mapping-relation.component';

export const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'performance-details',
                component: PerformanceDetailsComponent,
            },
            {
                path: 'performance-set',
                component: PerformanceSetComponent
            },
            {
                path: 'performance-history',
                component: PerformanceHistoryComponent,
                children: [
                    {
                        path: 'history-admin',
                        component: HistoryAdminComponent
                    },
                    {
                        path: 'history-person',
                        component: HistoryPersonComponent
                    }
                ]

            },
            {
                path: 'performance-details-org',
                component: PerformanceDetailsOrgComponent
            },
            {
                path: 'institutions-mapping',
                component: InstitutionsMappingComponent
            },
        ]
    }
]
export const PerformanceRouting: ModuleWithProviders = RouterModule.forChild(routes);